<template>
<div>
     <div> 
            <div style="display: inline-block;"> 镜像名：</div>
            <el-input v-model="search" prefix-icon="el-icon-search" style="display: inline-block;width: 350px" placeholder="请输入搜索内容"></el-input>
      </div>
      <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        highlight-current-row 
        style="width: 100%"
        @current-change="handleSelectionChange"> 
        <el-table-column
          label="操作"
          width="55">
          <template slot-scope="scope">
　　　　　　　　<el-radio v-model="checked" :label="scope.row.id"></el-radio> 
          </template>
        </el-table-column>
        <el-table-column
          label="日期"
          width="120">
          <template slot-scope="scope">{{ scope.row.date }}</template>
        </el-table-column>
        <el-table-column
          prop="name"
          label="姓名"
          width="120">
        </el-table-column>
        <el-table-column
          prop="address"
          label="地址"
          show-overflow-tooltip>
        </el-table-column>
      </el-table>
    </div>
  </template>


<script>
 export default {
    name: 'shopInfo',

    data () {
      return {
        tableData3: [],
        orginData:[],
　　　　　checked: null, // 如果使用单选框,定义一个model值
　　　　　currentSelectItem: {}, // 当前选中的值
          search:''
      }
    },

    created () {
      this.setTable()
    },

    methods: {
      setTable () {
        let resdata = [{
          id: 44,
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          id: 89,
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          id: 23,
          date: '2016-05-04',
          name: '张书寒',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          id: 88,
          date: '2016-05-07',
          name: '杨毅',
          address: '上海市普陀区金沙江路 1518 弄'
        }];
        this.orginData = resdata
      },

      handleSelectionChange (row) {
　　　　　// 如果使用单选框,这里可以把当前选中的这一项先保存起来
　　　　　this.currentSelectItem = row
      }
    },
    computed:{
      tableData(){
        this.tableData3 = [];
        this.orginData.forEach(item => {
          if(item.name.indexOf(this.search) != -1){
            this.tableData3.push(item)
          }
        })
        if(this.tableData3.length === 0){
          this.checked = false
          this.currentSelectItem = null
        }
        return this.tableData3
      }
    }
  }
</script>


